.Reservation {

    padding-top: 50px;

    .Reservation-header {
        width: 70%;
        margin-left: 15%;
        height: 50px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
        padding-top: 15px;

        @media (max-width: 991.98px) {
            width: 100%;
            margin-left: 0;
        }

        li {
            color: rgb(65, 65, 65);
            font-size: 15px;
            font-weight: 600;
            cursor: pointer;
            width: 10%;
            text-align: center;

            @media (max-width: 991.98px) {
                font-size: 14px;
            }

            @media (max-width: 767.98px) {}
        }
    }
}